import React from 'react';
import { Col, Button, Row} from 'react-bootstrap';
import  EnglishNames  from "./../data/EnglishNames.json";

const EnglishList = () => {
    
    let list = JSON.parse(JSON.stringify(EnglishNames)); 
       
    const {title1} = {title1: 
    <div>
    <h2 style={{ color: '#083d8d' }} className='mb-3'>Bienvenido a tu curso de INGLES</h2>
    <h2 style={{ color: '#083d8d' }} className='mb-3'>Por favor seleccione el nivel:</h2></div>}
    
    const {listCourses} = {  listCourses: 
      list[1].Names.map((item, index) => (
        <Col xs={10} sm={6} lg={3}  className="pl-5 mr-5">
         <div key={index} className="d-grid gap-2 p-3">
                 { <Button 
                    href={
                      list[0].Names.concat(list[1].URLNames[index])
                    }
                     variant="primary" size="lg" className="list-button"
                     >
                 {item}
                   </Button> }
                 
             </div>
         </Col>
         ))} 

 return (
    <div>
        <div className="component mt-5 pt-5">
             <Row className="justify-content-md-center">
             <Col xs={10} >
                  {title1}
             </Col>
             </Row> 
             <Row className="justify-content-md-center m-5">
             {listCourses}           

                 
             <div className="mb-5"></div>           
             </Row> 
        </div>
    </div>
    )
}

export default EnglishList

