import React, { useState } from 'react';
import { Col,  Button,   Row, OverlayTrigger, Tooltip } from 'react-bootstrap';
import EnglishLesson from './EnglishLesson';
import { useParams, useLocation} from "react-router-dom";
import  EnglishNames  from "./../data/EnglishNames.json";
import  LessonsNames  from "./../data/LessonsNames.json";


const EnglishList = () => {

    const { listLessonsURL } = useParams();
    const location = useLocation();

    let list = JSON.parse(JSON.stringify(EnglishNames)); 
    let names = JSON.parse(JSON.stringify(LessonsNames)); 
        
    //check for words in url 
    var count = (location.pathname.match(/\\,/g));
    let URLnames = location.pathname.split('/');
    // names[1] cursos_ingles --- names[2]: nivel 
    let levelID =  list[1].URLNames.indexOf(URLnames[2]);
   
    const {title2} = {title2: 
        <div className="div">
            <h3 className='mb-3' style={{ color: '#083d8d' }}>Bienvenido al curso de Ingles {list[1].Names[levelID]} </h3>
            <h3 style={{ color: '#083d8d' }} className='mb-3'>Seleccione la lección:</h3>
        </div>
    }

    //let lessonPop = names.filter((item) =>  (item.id >= (levelID + 1)*20 && item.id <= (levelID + 15)*20));
    let lessonPop = names.filter((item) =>  (item.id >= levelID*20 + 20 && item.id <= levelID*20 + 38));

    const {listClasses} = {listClasses:
    list[2].URLNames.map((item, index) => (
        <Col xs={10} sm={4} >
         <div key={index} className="d-grid gap-1 p-3">
            <OverlayTrigger placement='top'
                overlay={<Tooltip className="my-tooltip">{lessonPop[index].name}</Tooltip>} >
                 <Button 
                     href = {location.pathname.concat('/').concat(item)}
                     variant="primary" size="lg" className="list-button" >
                  {list[2].Names[index]}
                 </Button>
                 </OverlayTrigger>
             </div>
         </Col>
    ))}
    
    
    return (
    <div>
        <div className="component mt-5 pt-5">
             <Row className="justify-content-md-center">
              <Col xs={10} >
                   {title2}                                 
              </Col>
              </Row>
 
              <Row className="justify-content-md-center m-5">
                 {URLnames.length == 3 ? listClasses : <EnglishLesson />}
                 <div className="mb-5"></div>           
             </Row> 
        </div>
    </div>
    )
}

export default EnglishList

