import React, { useState } from 'react';
import AudioPlayer from 'react-h5-audio-player';
import 'react-h5-audio-player/lib/styles.css';
import { useParams, useLocation } from "react-router-dom";
import { Carousel, Col, Row } from 'react-bootstrap';
import '../App.css';
import  EnglishA11  from "./../data/EnglishA11.json";
import  EnglishA12  from "./../data/EnglishA12.json";
import  EnglishA21  from "./../data/EnglishA21.json";
import  EnglishA22  from "./../data/EnglishA22.json";
import  EnglishB11  from "./../data/EnglishB11.json";
import  EnglishB12  from "./../data/EnglishB12.json";
import  EnglishNames  from "./../data/EnglishNames.json";


const EnglishLesson = () => {
    const { lessonURL } = useParams()

  const location = useLocation();
  let names = location.pathname.split('/');
      // names[2]: nivel names[3] leccion 
  
  let list = JSON.parse(JSON.stringify(EnglishNames)); 
  var indexLevel = list[1].URLNames.indexOf(names[2]);
  var indexLesson = list[2].URLNames.indexOf(names[3]);
  var lessonID = (indexLevel+1)*20 + indexLesson+1;  

  let lessonsData = {} ;

  if (lessonID < 60){  lessonsData = JSON.parse(JSON.stringify(EnglishA11)); }
  else if (lessonID < 100){  lessonsData = JSON.parse(JSON.stringify(EnglishA12)); }
  else if (lessonID < 136){  lessonsData = JSON.parse(JSON.stringify(EnglishA21)); }
  else if (lessonID < 176){  lessonsData = JSON.parse(JSON.stringify(EnglishA22)); }
  else if (lessonID < 216){  lessonsData = JSON.parse(JSON.stringify(EnglishB11)); }
  else if (lessonID < 256){  lessonsData = JSON.parse(JSON.stringify(EnglishB12)); }

  let lesson = lessonsData.filter((item) =>  (item.lesson == lessonID));


 return (
      <div className=".container mt-3 p-5  ">
          <Row >
             <h4 style={{ color: '#083d8d' }}>Nivel {list[1].Names[indexLevel]} - {list[2].Names[indexLesson]}  </h4>
             <h3 className="text-center pb-4" style={{ color: '#083d8d' }}>Escucha, lee y repite 3 veces</h3>
             
          </Row>
            <Row >
           
                    <Carousel variant="dark" className="slides pb-3" >
                    {lesson.map((data, index) =>   
                    <Carousel.Item interval={10000}>
                        <Col xs={4}>
                        <AudioPlayer className="audio-slides" 
                        showJumpControls={false} layout="horizontal" customAdditionalControls={[]} customVolumeControls={[]}  src={data.audio} />
                        <img thumbnail className="d-block w-100 img-slides"
                            src = {data.picture}
                        />
                        
                        </Col>
                        <Col xs={8}>
                         <Carousel.Caption d-block w-100 className="text-slides">
                             <div dangerouslySetInnerHTML={{__html: data.caption}} /> 
                        </Carousel.Caption>
                        </Col>
                    </Carousel.Item>
                    )}
                    </Carousel>
                   
            </Row>
        </div>
    
    )
}

export default EnglishLesson